import Sprite from "../img/Sprites.png";

const game = document.querySelector(".game__area");
const root = game.getContext("2d");

const menuButtonParent = document.querySelector(".menu__buttons"),
      menuBody = document.querySelector(".menu"),
      scoreCurrentEl = document.querySelector('#score__current'),
      scoreBestEl = document.querySelector('#score__best');

game.width = 600; //window.innerWidth
game.height = 745; //window.innerHeight

const gravity = 0.14;
let bestScore = 0;
let startGame = false;
class Bird{
  constructor() {
    this.position = {
      x: game.width / 2,
      y: game.height / 2,
    }
    this.positionCurrent = {
      x: this.position.x,
      y: this.position.y,
    }
    this.size = {
      width: 33,
      height: 24,
    }
    this.velocity = {
      x: 0,
      y: 3
    }
    this.jump = {
      height: 0,
      length: 10,
      count: 0,

    }
    this.angle = 0;
    this.radius = 12;
    this.frame = {
      current: 0,
      slow: 0,
    };
  }


  draw(){
    root.save();
    root.translate(this.position.x,this.position.y);
    root.rotate(this.angle*Math.PI / 180);
    root.drawImage(
      createImage(Sprite),
      3 + 28 * this.frame.current,
      490,
      17,
      13,
      -this.size.width/2,
      -this.size.height/2,
      this.size.width,
      this.size.height);
    root.restore();
  }

  update(){
    this.frame.slow++;
    if(this.frame.slow > 7){
      if(this.frame.current > 1)this.frame.current = 0;
      this.frame.current++;
      this.frame.slow = 0;
    } 
    if (startGame) {
      this.angle += 4;
      this.position.y += this.velocity.y - this.jump.height;
      this.velocity.y += gravity;
    }
    if (this.angle > 90) {
      this.angle = 90;
    } 
    this.draw();
    
    this.jumpBird();
    this.collisionWall();
  }

  collisionWall(){
    if(this.position.y + platform.size.height > game.height){
       endGame();
    } 
    if(this.position.y  < 0){
      endGame();
    } 
  }
  jumpBird(){
    if(keys.space.pressed){
      this.jump.count++;
      this.angle = -30;
      this.jump.height = this.jump.length*Math.sin(Math.PI*this.jump.count/this.jump.length);
    }
    if(this.jump.count > this.jump.length) {
      this.jump.count = 0;
      this.velocity.y = 0;
      this.jump.height = 0;
      keys.space.pressed = false;
    }
  }
};

class Pipe {
  constructor(x = 0,y = 0,side = true) {
    this.position = {
      x:x,
      y:y
    }
    this.size = {
      width: 60,
      height: 700,
    }
    this.imageSideTop = {
      x: 56,
    }
    this.imageSideBottom = {
      x: 84,
    }
    this.side = side;
    if(this.side) {
      this.imageSide = this.imageSideTop;
    } else {
      this.imageSide = this.imageSideBottom;
    }
    this.scoreOne = false;
  }

  draw(){
    root.drawImage(
      createImage(Sprite), 
      this.imageSide.x, 
      323, 
      26, 
      160, 
      this.position.x,
      this.position.y,
      this.size.width,
      this.size.height);
  }
  update(){
    this.draw();
    this.position.x -= 2.2;
  }
  randomHeight(){
    return Math.floor(Math.random() * ((-120) - (-600) + 1) + (-600));
  }
};

class Platform {
  constructor(){
    this.size = {
      width: game.width,
      height: 75,
    }
    this.position = {
      x:0,
      y:game.height - this.size.height,
    }
  }

  draw(){
    root.drawImage(
      createImage(Sprite), 
      292, 
      0, 
      152, 
      56, 
      this.position.x,
      this.position.y,
      this.size.width,
      this.size.height);
  }
}

class Score {
  constructor(){
    this.position = {
      x: 10,
      y: 50
    }

    this.setup = 'bold 50px Montserrat';
  }

  draw(scoreNumber = 0){
    root.fillStyle =  '#ddd';
    root.font = this.setup;
    root.strokeText(scoreNumber, this.position.x, this.position.y);
    root.fillText(scoreNumber, this.position.x, this.position.y);
  }
}

//create bird
let bird = new Bird();
//create pipe 
let pipesPair = [];
let scoreNumber = 0;
let score = new Score();
let platform = new Platform();

// to check if a user pressed the space bar 
let keys = {
  space: {
    pressed: false,
  },
}; 

function init () {
  bird = new Bird();
  pipesPair = [];
  scoreNumber = 0;
  score = new Score();
  platform = new Platform(); 
  startGame = false;
}

// function for creating image
function createImage(imageScr){
  const image = new Image();
  image.src = imageScr;
  return image;
}



// Collision between Bird and Pipes
function ditectionCollisionBirdAndPipes(pipeCollision) {
  let test = {
      x: bird.position.x, 
      y: bird.position.y
  }

  // which edge is closest?
  if (bird.position.x < pipeCollision.position.x)  {
    test.x = pipeCollision.position.x;   
  } else if (bird.position.x > pipeCollision.position.x + pipeCollision.size.width) {
    test.x = pipeCollision.position.x + pipeCollision.size.width; 
  }   
  
  if (bird.position.y < pipeCollision.position.y)  {
    test.y = pipeCollision.position.y;
  }  else if (bird.position.y > pipeCollision.position.y + pipeCollision.size.height) {
    test.y = pipeCollision.position.y + pipeCollision.size.height;
  } 


  // get distance from closest edges
  let dist = {
    y: bird.position.y - test.y,
    x: bird.position.x - test.x
  }

  let distance = Math.sqrt( (dist.x**2) + (dist.y**2) );

  if (distance <= bird.radius)return true;
  return false;
}
// function for creating pairs of pipes 
function createPairOfPipes() {
  let min = -630,
      max = -200;

  let pipes = [];
  let randomY = Math.floor(Math.random() * ((max) - (min) + 1) + min);
  pipes[0] = new Pipe(game.width + 600,randomY);
  pipes[1] = new Pipe(game.width + 600, randomY + 800, false);
  pipesPair.push(pipes);
}
setInterval(()=>{
  if ( startGame) {
    createPairOfPipes();
  }
  
}, 2500);



// end game
function endGame () {
  cancelAnimationFrame(animationId);
  menuBody.style.display = "block";
  scoreCurrentEl.innerHTML = scoreNumber;
  if (bestScore < scoreNumber) {
    bestScore = scoreNumber;
    scoreBestEl.innerHTML = bestScore;
  }
  init ();
}
//main function
let animationId;

function animation(){
  animationId = requestAnimationFrame(animation);
  root.drawImage(createImage(Sprite), 0, 0, 144, 256, 0, 0, game.width, game.height);
  pipesPair.forEach((pipePair)=>{
    pipePair.forEach( function(pipe) {
      pipe.update();
      if(ditectionCollisionBirdAndPipes(pipe)){
        endGame();
      }
    });
    if(pipePair[0].position.x +  pipePair[0].size.width < -400){
      pipesPair.splice(0,1);
    }
    if(pipePair[0].position.x +  pipePair[0].size.width < game.width/2
      && pipePair[0].scoreOne == false){
      pipePair[0].scoreOne = true;
      scoreNumber++;
    }
  });
  platform.draw();
  bird.collisionWall();
  score.draw(scoreNumber);
  root.save();
  bird.update();
}






menuButtonParent.addEventListener('click',(event)=>{
  if (event.target && event.target.textContent == 'START') {
      menuBody.style.display = "none";
      animation();
  }
});


document.addEventListener('keydown',({keyCode}) => {
  switch (keyCode) {
    case 32:
      startGame = true;
      keys.space.pressed = true;
      break;
  }
});

